/* src/App.css */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #f4f4f4;
  }
  
  .App {
    text-align: center;
  }
  
  h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  button {
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  
  .container {
    width: 80%;
    margin: 0 auto;
    padding: 20px;
  }
  