src/components/Skills.css
.skills {
    padding: 4rem 2rem;
    background-color: #f8f9fa;
    text-align: center;
  }
  
  .skills-title {
    font-size: 2.5rem;
    margin-bottom: 2rem;
    color: #333;
    font-weight: bold;
  }
  
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
  }
  
  .skills-category {
    flex: 1;
    min-width: 300px;
    max-width: 600px;
    background: #ffffff;
    border-radius: 8px;
    padding: 2rem;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  }
  
  .skill-list {
    margin-bottom: 2rem;
  }
  
  .skill-title {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    color: #1e90ff;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .skill-title svg {
    font-size: 1.5rem;
  }
  
  .skill-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .skill-list li {
    font-size: 1rem;
    margin-bottom: 0.5rem;
    color: #666;
  }
  